module.exports = function (work) {
  var pending = null;
  var callback = null;
  var callbacks = null;
  var next = null;
  return function (val, cb) {
    next = val;
    update(cb || noop);
  };

  function update(cb) {
    if (callback) {
      if (!pending) pending = [];
      pending.push(cb);
      return;
    }

    var val = next;
    next = null;
    callback = cb;
    work(val, done);
  }

  function done(err) {
    var cb = callback;
    var cbs = callbacks;
    callbacks = null;
    callback = null;

    if (pending) {
      callbacks = pending;
      pending = null;
      update(noop);
    }

    if (cbs) {
      for (var i = 0; i < cbs.length; i++) {
        cbs[i](err);
      }
    }

    cb(err);
  }
};

function noop(_) {}